<script>
  export default {
   data (){
     return {
       r: 1
     }
   },
    render(h){
     console.log('%c vnodechildrender','color:#f00')
     return h('section',{attrs:{id:'sec'},class:{'render-dom':true,'data-added-class':this.r<3},on:{click:()=>{
       this.r += 1
     }}},[this.$slots.default, this.r, h('br')])
   },
    updated(){
      console.log(this,this.$vnode)
    },
    mounted(){
      var vm = this;
      console.log('*******childmounted',vm===vm.$vnode.componentInstance);

      console.log(vm.$vnode === vm.$options._parentVnode, vm.$vnode)
      console.log('-----------------------------------------------------');
      console.dir(vm.$el);
      var span= document.createElement('span');
      span.textContent ='appended span'
      document.querySelector('.render-dom').appendChild(span);
      setTimeout(function(){
        document.querySelector('.render-dom').classList.add('auto-added-class');
      },3000)
    }
  }
</script>


